import { createStore, combineReducers, applyMiddleware } from 'redux'
import thunk from 'redux-thunk'
import { composeWithDevTools } from 'redux-devtools-extension'
import { 
    userLoginReducer, 
    addConsultantReducer, 
    fetchManagersReducer, 
    fetchTeamLeadsReducer, 
    fetchConsultantsReducer, 
    fetchASMReducer,
    userUpdateReducer,
    userDetailReducer,
    deleteUserReducer, 
    userListReducer,
    fetchTeamLeadsUnderManagerReducer,
    fetchConsultantsUnderTeamLeadReducer,
    fetchConsultantsUnderManagerReducer,
    cpmListDataReducer,
    fetchLeadAssignerReducer,
    resetPasswordReducer
} from './reducers/usersReducer'
import { 
    addLeadReducer,
    updateLeadReducer,
    leadDetailsReducer,
    leadListReducer,
    deleteLeadReducer,
    bulkLeadsReducer,
    consultantLeadListReducer,
    teamLeadLeadListReducer,
    managerLeadListReducer,
    assignedTeamLeadLeadListReducer
} from './reducers/leadsReducer'
import { fetchProjectsReducer, addProjectReducer, updateProjectReducer, projectDetailsReducer, projectDeleteReducer } from './reducers/projectsReducer'
import { addCancelLeadReducer, cancelLeadListReducer, deleteCancelLeadReducer, updateCancelLeadReducer, cancelledLeadDetailsReducer, managerCancelLeadListReducer } from './reducers/cancellationLeadsReducer'
import { addBulkLeadReducer, assignBulkLeadReducer,getBulkLeadReducer,editBulkLeadReducer, leadAssignerCreateBulkLeadReducer, assignToManagerBulkLeadReducer, assignBulkLeadToTeamleadReducer } from './reducers/bulkLeadReducer'
import {activityLogDetailReducer} from './reducers/activityLogReducer'
import {addTaskReducer, fetchTasksReducer, fetchIndividualTasksReducer, deleteTaskReducer, updateTaskReducer} from './reducers/taskManagerReducer'
import {projectTeamDetailsReducer, addTeamReducer, deleteTeamReducer, getTeamListReducer, updateTeamDataReducer, getManagerTeamListReducer} from './reducers/teamManagementReducer'
import {cpmAddLeadReducer, cpmUpdateLeadReducer, cpmLeadDetailsReducer ,cpmLeadListReducer, cpmDeleteLeadReducer} from './reducers/cpManagerLeadsReducer'
import { leadAssignerAddBulkLeadReducer, leadAssignerAssigntoManagerReducer, leadAssignerLeadsReducer, leadAssignerBulkLeadsReducer } from './reducers/leadAssingerReducer'

const reducer = combineReducers({
    userLogin: userLoginReducer,
    addConsultant: addConsultantReducer,
    fetchManagers : fetchManagersReducer,
    fetchTeamLeads: fetchTeamLeadsReducer,
    fetchASM: fetchASMReducer,
    fetchConsultants: fetchConsultantsReducer,
    fetchLeadAssigner: fetchLeadAssignerReducer,
    fetchProjects: fetchProjectsReducer,
    addProject: addProjectReducer,
    updateProject: updateProjectReducer,
    projectDetails: projectDetailsReducer,
    projectDelete: projectDeleteReducer,
    deleteUserProfile: deleteUserReducer ,
    userUpdate: userUpdateReducer,
    userList : userListReducer,
    userDetail : userDetailReducer,
    resetPassword: resetPasswordReducer,
    teamLeadsUnderManager : fetchTeamLeadsUnderManagerReducer,
    consultantsUnderTeamLead : fetchConsultantsUnderTeamLeadReducer,
    fetchConsultantsUnderManager : fetchConsultantsUnderManagerReducer,
    cpmListData: cpmListDataReducer,
    addLead: addLeadReducer,
    updateLead: updateLeadReducer,
    leadDetails : leadDetailsReducer,
    leadList: leadListReducer,
    deleteLead: deleteLeadReducer,
    bulkLeads: bulkLeadsReducer,
    deleteCancelLead: deleteCancelLeadReducer,
    addCancelLead: addCancelLeadReducer,
    cancelLeadList: cancelLeadListReducer,
    consultantLeadList: consultantLeadListReducer,
    teamLeadLeadList: teamLeadLeadListReducer,
    assignedTeamLeadLeadList: assignedTeamLeadLeadListReducer,
    managerLeadList: managerLeadListReducer,
    updateCancelLead : updateCancelLeadReducer,
    cancelledLeadDetails : cancelledLeadDetailsReducer,
    managerCancelLeadList: managerCancelLeadListReducer,
    addBulkLead: addBulkLeadReducer,
    assignLeads: assignBulkLeadReducer,
    getBulkLead: getBulkLeadReducer,
    editBulkLead : editBulkLeadReducer,
    assignBulkLeadToTeamlead : assignBulkLeadToTeamleadReducer,
    activityLogDetail: activityLogDetailReducer,
    addTask: addTaskReducer,
    fetchTasks: fetchTasksReducer,
    fetchIndividualTasks: fetchIndividualTasksReducer,
    deleteTask: deleteTaskReducer,
    updateTask: updateTaskReducer,
    projectTeamDetails: projectTeamDetailsReducer,
    addTeam: addTeamReducer,
    deleteTeam: deleteTeamReducer,
    getTeamList: getTeamListReducer,
    updateTeamData: updateTeamDataReducer,
    getManagerTeamList : getManagerTeamListReducer,
    leadAssignerCreateBulkLead : leadAssignerCreateBulkLeadReducer,
    assignToManagerBulkLead: assignToManagerBulkLeadReducer,
    cpmAddLead: cpmAddLeadReducer,
    cpmUpdateLead : cpmUpdateLeadReducer,
    cpmLeadDetails : cpmLeadDetailsReducer,
    cpmLeadList: cpmLeadListReducer,
    cpmDeleteLead: cpmDeleteLeadReducer,
    leadAssignerAddBulkLead : leadAssignerAddBulkLeadReducer,
    leadAssignerAssigntoManager : leadAssignerAssigntoManagerReducer,
    leadAssignerLeads: leadAssignerLeadsReducer,
    leadAssignerBulkLeads: leadAssignerBulkLeadsReducer
})

const userInfoFromStorage = localStorage.getItem('userInfo')
  ? JSON.parse(localStorage.getItem('userInfo'))
  : null

const initialState = {
    userLogin: { userInfo: userInfoFromStorage }
}

const middleware = [thunk]

const store = createStore(
    reducer,
    initialState,
    composeWithDevTools(applyMiddleware(...middleware))
  )
  
  export default store