import axios from 'axios'
import {
    BULK_LEAD_ADD_REQUEST,
    BULK_LEAD_ADD_SUCCESS,
    BULK_LEAD_ADD_FAIL,
    BULK_LEAD_ADD_RESET,
    BULK_LEAD_ASSIGN_REQUEST,
    BULK_LEAD_ASSIGN_SUCCESS,
    BULK_LEAD_ASSIGN_FAIL,
    BULK_LEAD_MANAGER_ASSIGN_TO_TEAMLEAD_REQUEST,
    BULK_LEAD_MANAGER_ASSIGN_TO_TEAMLEAD_SUCCESS,
    BULK_LEAD_MANAGER_ASSIGN_TO_TEAMLEAD_FAIL,
    BULK_LEAD_LIST_REQUEST,
    BULK_LEAD_LIST_SUCCESS,
    BULK_LEAD_LIST_FAIL,
    BULK_LEAD_EDIT_REQUEST,
    BULK_LEAD_EDIT_SUCCESS,
    BULK_LEAD_EDIT_FAIL,
    LEADASSIGNER_BULK_LEAD_ADD_REQUEST,
    LEADASSIGNER_BULK_LEAD_ADD_SUCCESS,
    LEADASSIGNER_BULK_LEAD_ADD_FAIL,
    LEADASSIGNER_BULK_LEAD_ASSIGN_REQUEST,
    LEADASSIGNER_BULK_LEAD_ASSIGN_SUCCESS,
    LEADASSIGNER_BULK_LEAD_ASSIGN_FAIL,
} from '../constants/bulkLeadConstants'
import { logout } from './userActions'
import swal from 'sweetalert';

export const addBulkLeadData = (leadData, addToast) => async (dispatch, getState) => {
    
    try {
      dispatch({
        type: BULK_LEAD_ADD_REQUEST,
      })

      const {
        userLogin: { userInfo },
      } = getState()
  
      const config = {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${userInfo.token}`,
        },
      }
      const { data } = await axios.post(`${process.env.REACT_APP_BASE_URL}/api/bulkLead/create`,{bulkLeadArray: leadData},config)

      if (addToast) {
        // addToast("Bulk leads Uploaded Successfully", { appearance: "success", autoDismiss: true });
        swal("Successful", "Bulk leads Uploaded Successfully!", "success");

      }
  
      dispatch({
        type: BULK_LEAD_ADD_SUCCESS,
        payload: data,
      })
  
    } catch (error) {
       if(error.response && error.response.status === 401){
      dispatch( logout())
    }

    // if (addToast) {
    //   // addToast("Something went wrong", { appearance: "error", autoDismiss: true });
      
    // }

    swal("Oops!", "Something went wrong", "error");
    
      dispatch({
        type: BULK_LEAD_ADD_FAIL,
        payload:
          error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
      })
    }
  }

  export const assignBulkLead = (leadData, addToast) => async (dispatch, getState) => {
    try {
      dispatch({
        type: BULK_LEAD_ASSIGN_REQUEST,
      })

      const {
        userLogin: { userInfo },
      } = getState()
  
      const config = {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${userInfo.token}`,
        },
      }

      if( !leadData[0].isUploadedByLeadAssigner){
  
      const { data } = await axios.put(
        `${process.env.REACT_APP_BASE_URL}/api/bulkLead/assign/consultant`,{},
        config
      )
      // if (addToast) {
      //   // addToast("Bulk leads Successfully Assigned", { appearance: "success", autoDismiss: true });
      // }
  
      dispatch({
        type: BULK_LEAD_ASSIGN_SUCCESS,
        payload: data,
      })
      }else{

      const { data } = await axios.put(
        `${process.env.REACT_APP_BASE_URL}/api/bulkLead/leadAssigner/mgr/assignConsultant`,{},
        config
      )

      // if (addToast) {
      //   addToast("Bulk leads Successfully Assigned", { appearance: "success", autoDismiss: true });
      // }

      swal("Successful", "Bulk leads Successfully Assigned!", "success");

  
      dispatch({
        type: BULK_LEAD_ASSIGN_SUCCESS,
        payload: data,
      })
      }

      

     
  
    } catch (error) {
       if(error.response && error.response.status === 401){
      dispatch( logout())
    }

    // if (addToast) {
    //   addToast("Something went wrong", { appearance: "error", autoDismiss: true });
    // }

    swal("Oops!", "Something went wrong", "error");


      dispatch({
        type: BULK_LEAD_ASSIGN_FAIL,
        payload:
          error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
      })
    }
  }

