import React, {useEffect, useState} from 'react'
import axios from 'axios';
import { useDispatch, useSelector } from 'react-redux'
import BootstrapTable, {TableHeaderColumn} from 'react-bootstrap-table-next'
import { Image, Alert} from '@themesberg/react-bootstrap';
import paginationFactory from 'react-bootstrap-table2-paginator';
import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";
import filterFactory, { textFilter, selectFilter, dateFilter, Comparator} from 'react-bootstrap-table2-filter'; 
import cellEditFactory, { Type } from 'react-bootstrap-table2-editor';
import ToolkitProvider, { CSVExport } from 'react-bootstrap-table2-toolkit';

import { fetchLeadList, updateLeadData, fetchleadDetails, deleteLead} from '../actions/leadActions';
import { addCancelLeadData } from '../actions/cancellationLeadActions'
import Preloader from '../components/Preloader';
import ReactLogo from '../assets/img/technologies/loader.png'

import './style.css'
import { logout } from '../actions/userActions';



const Leads = ({history}) => {


  const dispatch = useDispatch()
  var pageNum = 0
  // useEffect(() => {
  //   dispatch(fetchLeadList())
  // }, [])

  // const leadList = useSelector((state) => state.leadList)
  //   const { loading, error, leadInfo } = leadList

  const userLogin = useSelector((state) => state.userLogin)
    const { userInfo: user } = userLogin

    useEffect(()=>{
      if (user && user.isAdmin) {
       
      } else {
        history.push('/login')
      }
    }, [user, history])


  const [leadData, setLeadData] = useState([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(false)

  

         useEffect(() => {
           setTimeout(()=>{
            axios.get(`${process.env.REACT_APP_BASE_URL}/api/leads`)
            .then(res => {
                const lead = res.data;
                setLeadData(lead)
                setLoading(false)                     
            }).catch(function (error) {
              if(error.response && error.response.status === 401){
                dispatch( logout())
              }
              // console.log(error)
              setError(true)
            })
          }, 0)
          

         }, [])


        const editTableHandler = (lead) => {
          history.push(`/leads/${lead._id}/edit`)
        }
       

        const { ExportCSVButton } = CSVExport;

        const options = {
          
            sizePerPage: 10,
            
          onPageChange: (page, sizePerPage) => {
            pageNum = page - 1
          }
        };

        const filterByPrice = (filterVal, data) => {
          // console.log(filterVal)
          if (filterVal) {
            return data.filter(product => product.price == filterVal);
          }
          return data;
        }

    const columns = [
      {
        dataField: "key",
        text: "Serial No.",
        csvExport: false,
        formatter :(cell,row, index, formatExtraData) => {
          return (pageNum*10) + index+1
        },
        
      },
      {
        dataField: "customerName",
        text: "Customer",
        sort: true,
        filter: textFilter()
      },
      {
        dataField: "enquiryDate",
        text: "Enquiry Date",
        sort: true,
        formatter: (enquiryDate) => {
          if(enquiryDate){
          return new Date(`${enquiryDate}`).toISOString().split('T')[0]
          }
        },
        filter: dateFilter({
          defaultValue: {comparator: Comparator.GT }
        })
      },
      {
        dataField: "contactNumber",
        text: "Contact Number",
        filter: textFilter()
      },
      {
        dataField: "alternateNumber",
          text: "Alternate Number",
        filter: textFilter()
      },
      {
        dataField: "assignedConsultant",
        text: "Assigned to",
        // sort: true,
        // sortFunc: (a, b, order, dataField) => {
        //   if (order === 'asc') {
        //     return b?.userId?.name - a?.userId?.name;
        //   }
        //   return a?.userId?.name - b?.userId?.name; // desc
        // },
        // filter: textFilter({
        //   getFilter: (filter) => {
        //     onFilter: this.filterByPrice
        //     // nameFilter = filter;
        //     return
        //   }
        // }),
        csvFormatter : (cell,row, index, formatExtraData) => {
          return row.assignedConsultant? row.assignedConsultant?.userId?.name : row.assignedTeamLead?.userId?.name
        },
        formatter :(cell,row, index, formatExtraData) => {
          return row.assignedConsultant? row.assignedConsultant?.userId?.name : row.assignedTeamLead?.userId?.name
        },
      },
      
      {
        dataField: "project.mainProject",
        text: "Main Project",
        filter: textFilter()
      },
      {
        dataField: "project.subProject",
        text: "Sub Project",
        filter: textFilter()
      },
     
      {
        dataField: "leadScore",
        text: "lead Source",
        filter: textFilter()
      },
      {
        dataField: "leadType",
        text: "lead Type",
        filter: textFilter()
      },
      {
        dataField: "status",
        text: "Status",
        filter: textFilter()
      },
      {
        dataField: "remarks",
        text: "Remarks",
        filter: textFilter(),
      },
      {
        dataField: "nextFollowUp",
        text: "Next Follow Up Date",
        sort: true,
        formatter: (enquiryDate) => {
          if(enquiryDate){
          return new Date(`${enquiryDate}`).toISOString().split('T')[0]
          }
        },
        filter: dateFilter({
          defaultValue: {comparator: Comparator.GT }
        })
      },
      {
        dataField: "managerRemarks",
        text: "Manager Remarks",
        filter: textFilter()
      },
      {
        dataField: "",
        text: "",
        csvExport: false,
        formatter: (cell,row) => {
          return (
            <button className="btn btn-primary" onClick={ ()=>editTableHandler(row) }>Edit</button>
          );
        },

      },

    ];





  return (
    <>
    {loading? (
      <div className={`preloader bg-softr flex-column justify-content-center align-items-center `}>
      <Image className="loader-element animate__animated animate__jackInTheBox" src={ReactLogo} height={70} />
      </div>
    ): error?  (<Alert variant="primary">
    {error}
  </Alert>)
  :  (
    <div>
      <ToolkitProvider
        keyField="_id"
        data={ leadData }
        columns={ columns }
        exportCSV
      >
        {
          props => (
            <div>
              <ExportCSVButton { ...props.csvProps }>Export CSV</ExportCSVButton>
              <hr />
              <div className="bg-dark">
                <button className="btn btn-transparent text-white text-center">Total Leads {leadData?.length}</button>
              </div>
              {/* <BootstrapTable { ...props.baseProps } /> */}
              <BootstrapTable
                   { ...props.baseProps }
                  bootstrap4
                  keyField="_id"
                  data={leadData}
                  columns={columns}
                  // cellEdit={ cellEditFactory({ mode: 'click', blurToSave: true })}
                  filter={ filterFactory() }
                  pagination={paginationFactory(options)}
                  striped
                  table-dark
                />
                  
            </div>
          )
        }
      </ToolkitProvider>
    </div>
    )}
    </>
  )
}

export default Leads





