import axios from 'axios'

import {
    LEADASSIGNER_BULK_LEADS_ADD_REQUEST,
    LEADASSIGNER_BULK_LEADS_ADD_SUCCESS,
    LEADASSIGNER_BULK_LEADS_ADD_FAIL,
    LEADASSIGNER_BULK_LEADS_LIST_REQUEST,
    LEADASSIGNER_BULK_LEADS_LIST_SUCCESS,
    LEADASSIGNER_BULK_LEADS_LIST_FAIL,
    LEADASSIGNER_ASSIGN_TO_MANAGER_REQUEST,
    LEADASSIGNER_ASSIGN_TO_MANAGER_SUCCESS,
    LEADASSIGNER_ASSIGN_TO_MANAGER_FAIL,
    LEADASSIGNER_LEAD_LIST_REQUEST,
    LEADASSIGNER_LEAD_LIST_SUCCESS,
    LEADASSIGNER_LEAD_LIST_FAIL

} from '../constants/leaadAssignerConstants'
import { logout } from './userActions'

export const leadAssignerAddBulkLeadData = (leadData, addToast) => async (dispatch, getState) => {
    
    try {
      dispatch({
        type: LEADASSIGNER_BULK_LEADS_ADD_REQUEST,
      })

      const {
        userLogin: { userInfo },
      } = getState()
  
      const config = {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${userInfo.token}`,
        },
      }
  
      const { data } = await axios.post(
        `${process.env.REACT_APP_BASE_URL}/api/leadAssigner/create/BulkLead`,
        {bulkLeadArray: leadData},
        config
      )

      if (addToast) {
        addToast("Bulk leads Uploaded Successfully", { appearance: "success", autoDismiss: true });
      }
  
      dispatch({
        type: LEADASSIGNER_BULK_LEADS_ADD_SUCCESS,
        payload: data,
      })
  
    } catch (error) {
       if(error.response && error.response.status === 401){
      dispatch( logout())
    }

    if (addToast) {
      addToast("Something went wrong", { appearance: "error", autoDismiss: true });
    }
      dispatch({
        type: LEADASSIGNER_BULK_LEADS_ADD_FAIL,
        payload:
          error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
      })
    }
  }


  export const getLeadAssignerBulkLeadList = () => async (dispatch, getState) => {
    try {
      dispatch({
        type: LEADASSIGNER_BULK_LEADS_LIST_REQUEST,
      })

      const {
        userLogin: { userInfo },
      } = getState()
  
      const config = {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${userInfo.token}`,
        },
      }
  
      const { data } = await axios.get(
        `${process.env.REACT_APP_BASE_URL}/api/leadAssigner/lead/list`,
        config
      )
  
      dispatch({
        type: LEADASSIGNER_BULK_LEADS_LIST_SUCCESS,
        payload: data,
      })
  
    } catch (error) {
       if(error.response && error.response.status === 401){
      dispatch( logout())
    }
      dispatch({
        type: LEADASSIGNER_BULK_LEADS_LIST_FAIL,
        payload:
          error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
      })
    }
  }

