import {
    BULK_LEAD_ADD_REQUEST,
    BULK_LEAD_ADD_SUCCESS,
    BULK_LEAD_ADD_FAIL,
    BULK_LEAD_ADD_RESET,
    BULK_LEAD_ASSIGN_REQUEST,
    BULK_LEAD_ASSIGN_SUCCESS,
    BULK_LEAD_ASSIGN_FAIL,
    BULK_LEAD_ASSIGN_RESET,
    BULK_LEAD_EDIT_REQUEST,
    BULK_LEAD_EDIT_SUCCESS,
    BULK_LEAD_EDIT_FAIL,
    BULK_LEAD_LIST_REQUEST,
    BULK_LEAD_LIST_SUCCESS,
    BULK_LEAD_LIST_FAIL,
    LEADASSIGNER_BULK_LEAD_ADD_REQUEST,
    LEADASSIGNER_BULK_LEAD_ADD_SUCCESS,
    LEADASSIGNER_BULK_LEAD_ADD_FAIL,
    LEADASSIGNER_BULK_LEAD_ADD_RESET,
    LEADASSIGNER_BULK_LEAD_ASSIGN_REQUEST,
    LEADASSIGNER_BULK_LEAD_ASSIGN_SUCCESS,
    LEADASSIGNER_BULK_LEAD_ASSIGN_FAIL,
    BULK_LEAD_MANAGER_ASSIGN_TO_TEAMLEAD_REQUEST,
    BULK_LEAD_MANAGER_ASSIGN_TO_TEAMLEAD_SUCCESS,
    BULK_LEAD_MANAGER_ASSIGN_TO_TEAMLEAD_FAIL,
    BULK_LEAD_MANAGER_ASSIGN_TO_TEAMLEAD_RESET
} from '../constants/bulkLeadConstants'


export const addBulkLeadReducer = (state = {}, action) => {
    switch (action.type) {
      case BULK_LEAD_ADD_REQUEST:
        return { loading: true }
      case BULK_LEAD_ADD_SUCCESS:
        return { loading: false, status: action.payload }
      case BULK_LEAD_ADD_FAIL:
        return { loading: false, error: action.payload }
      case BULK_LEAD_ADD_RESET:
            return { }
      default:
        return state
    }
  }

  export const assignBulkLeadReducer = (state = {}, action) => {
    switch (action.type) {
      case BULK_LEAD_ASSIGN_REQUEST:
        return { loading: true }
      case BULK_LEAD_ASSIGN_SUCCESS:
        return { loading: false, success : true }
      case BULK_LEAD_ASSIGN_FAIL:
        return { loading: false, error: action.payload }
      case BULK_LEAD_ASSIGN_RESET:
        return {}
      default:
        return state
    }
  }

  export const assignBulkLeadToTeamleadReducer = (state = {}, action) => {
    switch (action.type) {
      case BULK_LEAD_MANAGER_ASSIGN_TO_TEAMLEAD_REQUEST:
        return { loading: true }
      case BULK_LEAD_MANAGER_ASSIGN_TO_TEAMLEAD_SUCCESS:
        return { loading: false, success : true }
      case BULK_LEAD_MANAGER_ASSIGN_TO_TEAMLEAD_FAIL:
        return { loading: false, error: action.payload }
      case BULK_LEAD_MANAGER_ASSIGN_TO_TEAMLEAD_RESET:
        return {}
      default:
        return state
    }
  }

  export const getBulkLeadReducer = (state = {}, action) => {
    switch (action.type) {
      case BULK_LEAD_LIST_REQUEST:
        return { loading: true }
      case BULK_LEAD_LIST_SUCCESS:
        return { loading: false, leadList : action.payload.bulkLeadList }
      case BULK_LEAD_LIST_FAIL:
        return { loading: false, error: action.payload }
      default:
        return state
    }
  }

  export const editBulkLeadReducer = (state = {}, action) => {
    switch (action.type) {
      case BULK_LEAD_EDIT_REQUEST:
        return { loading: true }
      case BULK_LEAD_EDIT_SUCCESS:
        return { loading: false, leadInfo: action.payload, success: true }
      case BULK_LEAD_EDIT_FAIL:
        return { loading: false, error: action.payload }
      default:
        return state
    }
  }

  export const leadAssignerCreateBulkLeadReducer = (state = {}, action) => {
    switch (action.type) {
      case  LEADASSIGNER_BULK_LEAD_ADD_REQUEST:
        return { loading: true }
      case  LEADASSIGNER_BULK_LEAD_ADD_SUCCESS:
        return { loading: false, status: action.payload }
      case  LEADASSIGNER_BULK_LEAD_ADD_FAIL:
        return { loading: false, error: action.payload }
      case  LEADASSIGNER_BULK_LEAD_ADD_RESET:
            return { }
      default:
        return state
    }
  }

  export const assignToManagerBulkLeadReducer = (state = {}, action) => {
    switch (action.type) {
      case LEADASSIGNER_BULK_LEAD_ASSIGN_REQUEST:
        return { loading: true }
      case LEADASSIGNER_BULK_LEAD_ASSIGN_SUCCESS:
        return { loading: false, success : true }
      case LEADASSIGNER_BULK_LEAD_ASSIGN_FAIL:
        return { loading: false, error: action.payload }
      default:
        return state
    }
  }