import axios from 'axios'
import {
    ADD_TEAM_REQUEST,
    ADD_TEAM_SUCCESS,
    ADD_TEAM_FAIL,
    FETCH_TEAM_LIST_REQUEST,
    FETCH_TEAM_LIST_SUCCESS,
    FETCH_TEAM_LIST_FAIL,
    MANAGER_TEAM_LIST_REQUEST,
    MANAGER_TEAM_LIST_SUCCESS,
    MANAGER_TEAM_LIST_FAIL,
    TEAM_DETAILS_REQUEST,
    TEAM_DETAILS_SUCCESS,
    TEAM_DETAILS_FAIL,
    DELETE_TEAM_REQUEST,
    DELETE_TEAM_SUCCESS,
    DELETE_TEAM_FAIL,
    UPDATE_TEAM_REQUEST,
    UPDATE_TEAM_SUCCESS,
    UPDATE_TEAM_FAIL
} from '../constants/teamManagementConstants';
import { logout } from './userActions';


/* Add team */
export const addTeam = (team, addToast) => async (dispatch,getState) => {
  try {
    dispatch({
      type: ADD_TEAM_REQUEST,
    })

    const {
      userLogin: { userInfo },
    } = getState()


    const config = {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${userInfo.token}`
      },
    }


    

    const { data } = await axios.post(
       `${process.env.REACT_APP_BASE_URL}/api/team/new`,team,
      config
    )

    if (addToast) {
      addToast("Team Added", { appearance: "success", autoDismiss: true });
    }

    dispatch({
      type: ADD_TEAM_SUCCESS,
      payload: data,
    })

  } catch (error) {
     if(error.response && error.response.status === 401){
      dispatch( logout())
    }
    dispatch({
      type: ADD_TEAM_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    })
  }
}


