  /* update team by id */
export const updateTeam = (team, addToast) => async (dispatch, getState) => {
  try {
    dispatch({
      type: UPDATE_TEAM_REQUEST,
    })

    const {
      userLogin: { userInfo },
    } = getState()


    const config = {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${userInfo.token}`
      },
    }


    

    const { data } = await axios.put(
      `${process.env.REACT_APP_BASE_URL}/api/team/${team._id}`, team,
      config
    )

    if (addToast) {
      addToast("User Updated", { appearance: "success", autoDismiss: true });
    }

    dispatch({
      type: UPDATE_TEAM_SUCCESS,
      payload: data,
    })

  } catch (error) {
     if(error.response && error.response.status === 401){
      dispatch( logout())
    }
    dispatch({
      type: UPDATE_TEAM_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    })
  }
}

