import {
    LEADASSIGNER_BULK_LEADS_ADD_REQUEST,
    LEADASSIGNER_BULK_LEADS_ADD_SUCCESS,
    LEADASSIGNER_BULK_LEADS_ADD_FAIL,
    LEADASSIGNER_BULK_LEADS_LIST_REQUEST,
    LEADASSIGNER_BULK_LEADS_LIST_SUCCESS,
    LEADASSIGNER_BULK_LEADS_LIST_FAIL,
    LEADASSIGNER_ASSIGN_TO_MANAGER_REQUEST,
    LEADASSIGNER_ASSIGN_TO_MANAGER_SUCCESS,
    LEADASSIGNER_ASSIGN_TO_MANAGER_FAIL,
    LEADASSIGNER_ASSIGN_TO_MANAGER_RESET,
    LEADASSIGNER_LEAD_LIST_REQUEST,
    LEADASSIGNER_LEAD_LIST_SUCCESS,
    LEADASSIGNER_LEAD_LIST_FAIL
    

} from '../constants/leaadAssignerConstants'

export const leadAssignerAddBulkLeadReducer = (state = {}, action) => {
    switch (action.type) {
      case LEADASSIGNER_BULK_LEADS_ADD_REQUEST:
        return { loading: true }
      case LEADASSIGNER_BULK_LEADS_ADD_SUCCESS:
        return { loading: false, leadAssignerLeads: action.payload }
      case LEADASSIGNER_BULK_LEADS_ADD_FAIL:
        return { loading: false, error: action.payload }
    //   case LEADASSIGNER_BULK_LEADS_ADD_RESET:
    //         return { }
      default:
        return state
    }
  }

  export const leadAssignerAssigntoManagerReducer = (state = {}, action) => {
    switch (action.type) {
      case LEADASSIGNER_ASSIGN_TO_MANAGER_REQUEST:
        return { loading: true }
      case LEADASSIGNER_ASSIGN_TO_MANAGER_SUCCESS:
        return { loading: false, leadAssignerLeads: action.payload, success: true }
      case LEADASSIGNER_ASSIGN_TO_MANAGER_FAIL:
        return { loading: false, error: action.payload }
      case LEADASSIGNER_ASSIGN_TO_MANAGER_RESET:
            return { }
      default:
        return state
    }
  }

  export const leadAssignerLeadsReducer = (state = {}, action) => {
    switch (action.type) {
      case LEADASSIGNER_LEAD_LIST_REQUEST:
        return { loading: true }
      case LEADASSIGNER_LEAD_LIST_SUCCESS:
        return { loading: false, leadAssignerLeads: action.payload }
      case LEADASSIGNER_LEAD_LIST_FAIL:
        return { loading: false, error: action.payload }
    //   case LEADASSIGNER_BULK_LEADS_ADD_RESET:
    //         return { }
      default:
        return state
    }
  }

  export const leadAssignerBulkLeadsReducer = (state = {}, action) => {
    switch (action.type) {
      case LEADASSIGNER_BULK_LEADS_LIST_REQUEST:
        return { loading: true }
      case LEADASSIGNER_BULK_LEADS_LIST_SUCCESS:
        return { loading: false, leadAssignerBulkLeads: action.payload }
      case LEADASSIGNER_BULK_LEADS_LIST_FAIL:
        return { loading: false, error: action.payload }
    //   case LEADASSIGNER_BULK_LEADS_ADD_RESET:
    //         return { }
      default:
        return state
    }
  }