import {
    CPM_LEAD_ADD_FAIL,
    CPM_LEAD_ADD_REQUEST,
    CPM_LEAD_ADD_SUCCESS,
    CPM_LEAD_UPDATE_REQUEST,
    CPM_LEAD_UPDATE_SUCCESS,
    CPM_LEAD_UPDATE_FAIL,
    CPM_LEAD_UPDATE_RESET,
    CPM_LEAD_DETAILS_REQUEST,
    CPM_LEAD_DETAILS_SUCCESS,
    CPM_LEAD_DETAILS_RESET,
    CPM_LEAD_DETAILS_FAIL,
    CPM_LEAD_LIST_REQUEST,
    CPM_LEAD_LIST_SUCCESS,
    CPM_LEAD_LIST_FAIL,
    CPM_LEAD_DELETE_REQUEST,
    CPM_LEAD_DELETE_SUCCESS,
    CPM_LEAD_DELETE_FAIL,

} from '../constants/cpManagerConstants';

export const cpmAddLeadReducer = (state = {}, action) => {
    switch (action.type) {
      case CPM_LEAD_ADD_REQUEST:
        return { loading: true }
      case CPM_LEAD_ADD_SUCCESS:
        return { loading: false, leadInfo: action.payload }
      case CPM_LEAD_ADD_FAIL:
        return { loading: false, error: action.payload }
      default:
        return state
    }
  }

export const cpmUpdateLeadReducer = (state = {}, action) => {
    switch (action.type) {
      case CPM_LEAD_UPDATE_REQUEST:
        return { loading: true }
      case CPM_LEAD_UPDATE_SUCCESS:
        return { loading: false, leadInfo: action.payload, success: true }
      case CPM_LEAD_UPDATE_FAIL:
        return { loading: false, error: action.payload }
        case CPM_LEAD_UPDATE_RESET:
          return { }
      default:
        return state
    }
  }

