import React, {useEffect, useState} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Image } from '@themesberg/react-bootstrap';
import { consultantsUnderManager, fetchUserList, teamLeadsUnderManager } from '../actions/userActions'
import { deleteUser } from '../actions/userActions';

import ReactLogo from '../assets/img/technologies/loader.png'

import UserTable from './UserTable';


const UserList = ({history}) => {
    const [userList, setUserList] = useState([])
  
    const dispatch = useDispatch()

    const userLogin = useSelector((state) => state.userLogin)
    const { userInfo: user } = userLogin

    const teamLeadsListManager = useSelector((state) => state.teamLeadsUnderManager)
    const { loading, error, teamleadList } = teamLeadsListManager

    const fetchConsultantsUnderManager = useSelector((state) => state.fetchConsultantsUnderManager)
    const { consultantsList } = fetchConsultantsUnderManager

    useEffect(()=>{
      if (user && user.role === "Manager") {
        dispatch(teamLeadsUnderManager(user.managerId))
        dispatch(consultantsUnderManager(user.managerId))
      } else {
        history.push('/login')
      }
    }, [user, history])

    
    useEffect(()=>{
      if(teamleadList && consultantsList){
        setUserList([...teamleadList.teamLeadsList, ...consultantsList])
      }

    }, [teamleadList, consultantsList])
  
  
  
  if(userList.length === 0){
  return (
    // <div className={`preloader bg-softr flex-column justify-content-center align-items-center `}>
    //     <Image className="loader-element animate__animated animate__jackInTheBox" src={ReactLogo} height={70} />
    //     </div>

    <div>No List Found</div>
    
  )
  } else{
    
    return (
      
      <>
      {loading? (
        // <div className={`preloader bg-softr flex-column justify-content-center align-items-center `}>
        // <Image className="loader-element animate__animated animate__jackInTheBox" src={ReactLogo} height={40} />
        // </div>
        <div></div>
      ) : (
      <div>
        
                    <UserTable users={userList}/>
      </div>
      )}
      </>
    )
  }
}
  

  export default UserList

