import React from 'react'

const Header = () => {
  return (
    <div className="page-wrapper container">
      {/* <!-- Preloader --> */}
      {/* <div className="preloader"></div> */}
      {/* <!--====== Header Part Start ======--> */}
      
      <header className="main-header header-three">
        {/* <!--Header-Upper--> */}
        <div className="header-upper">
          <div className="container clearfix">
            <div className="header-inner py-20">
              <div className="logo-outer">
                <div className="logo">
                  <a href="#">
                    <img
                      src="assets/images/client-logos/crm1.png"
                      height="30px"
                      alt="Logo"
                    />
                  </a>
                </div>
              </div>
              <div className="nav-outer d-flex align-items-center clearfix mx-lg-auto">
                {/* <!-- Main Menu --> */}
                <nav className="main-menu navbar-expand-lg">
                  <div className="navbar-header">
                    <div className="logo-mobile">
                      <a href="#">
                        <img
                          src="assets/images/client-logos/crm1.png"
                          height="30px"
                          alt="Logo"
                        />
                      </a>
                    </div>
                    {/* <!-- Toggle Button --> */}
                    <button
                      type="button"
                      className="navbar-toggle"
                      data-toggle="collapse"
                      data-target=".navbar-collapse"
                      aria-controls="main-menu"
                    >
                      <span className="icon-bar"></span>
                      <span className="icon-bar"></span>
                      <span className="icon-bar"></span>
                    </button>
                  </div>
                </nav>
              </div>

              <div className="menu-right d-none d-lg-flex align-items-center">
                <a href="https://brccrm.com/#/" className="theme-btn style-two">
                  Login <i className="fas fa-arrow-right"></i>
                </a>
              </div>
            </div>
          </div>
        </div>
        {/* <!--End Header Upper--> */}
      </header>
      {/* <!--====== Header Part End ======--></>) */}
    </div>
  )
}

export default Header
