import React, {useEffect, useState} from 'react'
import axios from 'axios';
import { useDispatch, useSelector } from 'react-redux'
import BootstrapTable, {TableHeaderColumn} from 'react-bootstrap-table-next'
import paginationFactory from 'react-bootstrap-table2-paginator';
import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";
import filterFactory, { textFilter, dateFilter, Comparator} from 'react-bootstrap-table2-filter'; 
import ToolkitProvider, { CSVExport } from 'react-bootstrap-table2-toolkit';

// import ReactLogo from '../assets/img/technologies/react-logo-transparent.svg'

import ReactLogo from '../assets/img/technologies/loader.png'
import { Image, Alert } from '@themesberg/react-bootstrap';

import './style.css'
import { useHistory } from 'react-router-dom';
import { logout } from '../actions/userActions';



const NextFollowUp = () => {
  var pageNum = 0
  var gapi = window.gapi

  const history = useHistory()
  var CLIENT_ID = "1013896874235-1htic5jfq2uhg6s8kjt7130t98a0kd86.apps.googleusercontent.com"
    var API_KEY = "AIzaSyBoJQl0S2P5iI5LxS4FQThPKm8b5rVagwc"
    var DISCOVERY_DOCS = ["https://www.googleapis.com/discovery/v1/apis/calendar/v3/rest"]
    var SCOPES = "https://www.googleapis.com/auth/calendar.events"

  const [remainder, setRemainder] = useState(false) 

  // var element = document.getElementsByTagName("table");
  // element.classList.add("table-dark");

  const dispatch = useDispatch()

  // useEffect(() => {
  //   dispatch(fetchLeadList())
  // }, [])

  // const leadList = useSelector((state) => state.leadList)
  //   const { loading, error, leadInfo } = leadList

  const userLogin = useSelector((state) => state.userLogin)
    const { userInfo: user } = userLogin

    useEffect(()=>{
      if (user && user.role === "TeamLead") {
       
      } else {
        history.push('/login')
      }
    }, [user, history])

  const selectOptions = {
    0: 'Hot',
    1: 'Warm',
    2: 'Cold'
  };

  const selectOptions2 = {
    0: 'Yet to Visit',
    1: 'Visited',
    2: 'Visit Confirmed',
    3: 'Detail Shared',
    4: 'Lost',
    5: 'Booked'
  };

  const [leadData, setLeadData] = useState([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')

         useEffect(() => {
          const config = {
            headers: {
              Authorization: `Bearer ${user.token}`,
            },
          }

            axios.get(`${process.env.REACT_APP_BASE_URL}/api/leads/teamlead/all`, config)
            .then(res => {
                const today = new Date()
                const lead = res.data.leadsList.filter(lead => {if(lead.nextFollowUp){return lead.nextFollowUp.split('T')[0]  ===  today.toISOString().split('T')[0]}});
                setLeadData(lead)   
                setLoading(false)                   
            }).catch(function(error){
              if(error.response && error.response.status === 401){
                dispatch( logout())
              }
              setError(error?.response?.data?.message)
              setLoading(false)  
            })
         }, [])


        const editTableHandler = (lead) => {
          history.push(`/teamlead/leads/${lead._id}/edit`)
        }

      

        
       

        const { ExportCSVButton } = CSVExport;

        const options = {
          
          sizePerPage: 10,
          
        onPageChange: (page, sizePerPage) => {
          pageNum = page - 1
        }
      };

    const columns = [
      {
        dataField: "_id",
        text: "Serial No.",
        formatter :(row,index, leadData) => {
          return (pageNum*10)+leadData+1
        },
        
      },
      {
        dataField: "customerName",
        text: "Customer",
        sort: true,
        filter: textFilter()
      },
      {
        dataField: "enquiryDate",
        text: "Enquiry Date",
        sort: true,
        formatter: (enquiryDate) => {
          if(enquiryDate){
          return new Date(`${enquiryDate}`).toISOString().split('T')[0]
          }
        },
        filter: dateFilter({
          defaultValue: {comparator: Comparator.GT }
        })
      },
      {
        dataField: "contactNumber",
        text: "Contact Number",
        filter: textFilter()
      },
      {
        dataField: "alternateNumber",
          text: "Alternate Number",
        filter: textFilter()
      },
      {
        dataField: "assignedConsultant.userId.name",
        text: "Assign To",
        sort: true,
        filter: textFilter(),
        csvFormatter : (cell,row, index, formatExtraData) => {
          return row.assignedConsultant? row.assignedConsultant?.userId?.name : row.assignedTeamLead?.userId?.name
        },
        formatter :(cell,row, index, formatExtraData) => {
          return row.assignedConsultant? row.assignedConsultant?.userId?.name : row.assignedTeamLead?.userId?.name
        },
      },
      {
        dataField: "project.subProject",
        text: "Project",
        filter: textFilter()
      },
     
      {
        dataField: "leadScore",
        text: "lead Score",
        filter: textFilter()
      },
      {
        dataField: "leadType",
        text: "lead Type",
        filter: textFilter()
      },
      {
        dataField: "status",
        text: "Status",
        filter: textFilter()
      },
      {
        dataField: "remarks",
        text: "Remarks",
        filter: textFilter()
      },
      {
        dataField: "nextFollowUp",
        text: "Next Follow Up Date",
        sort: true,
        formatter: (enquiryDate) => {
          if(enquiryDate){
          return new Date(`${enquiryDate}`).toISOString().split('T')[0]
          }
        },
        filter: dateFilter({
          defaultValue: {comparator: Comparator.GT }
        })
      },
      {
        dataField: "managerRemarks",
        text: "Manager Remarks",
        filter: textFilter()
      },
      // {
      //   dataField: "",
      //   text: "",
      //   formatter: (cell,row) => {
      //     return (
      //       <button className="btn btn-primary" onClick={ ()=>remainderHandler(row) }>Remainder</button>
      //     );
      //   },

      // },
      {
        dataField: "",
        text: "",
        formatter: (cell,row) => {
          return (
            <button className="btn btn-primary" onClick={ ()=>editTableHandler(row) }>Edit</button>
          );
        },

      },

    ];





  return (
    <>
    {loading? (
      <div className={`preloader bg-softr flex-column justify-content-center align-items-center `}>
      <Image className="loader-element animate__animated animate__jackInTheBox" src={ReactLogo} height={70} />
      </div>
    ): error?  (<Alert variant="primary">
    {error}
</Alert>): (
    <div>
      <ToolkitProvider
        keyField="_id"
        data={ leadData }
        columns={ columns }
        exportCSV
      >
        {
          props => (
            <div>
              <ExportCSVButton { ...props.csvProps }>Export CSV</ExportCSVButton>
              <hr />
              <div className="bg-dark">
                <button className="btn btn-transparent text-white text-center">Total Leads {leadData?.length}</button>
              </div>
              {/* <BootstrapTable { ...props.baseProps } /> */}
              <BootstrapTable
                   { ...props.baseProps }
                  bootstrap4
                  keyField="_id"
                  data={leadData}
                  columns={columns}
                  // cellEdit={ cellEditFactory({ mode: 'click', blurToSave: true })}
                  filter={ filterFactory() }
                  pagination={paginationFactory(options)}
                  striped
                  table-dark
                />
                  
            </div>
          )
        }
      </ToolkitProvider>
    </div>
    )}
    </>
  )
}

export default NextFollowUp





