import React,{useState, useEffect, useRef} from 'react'
import axios from 'axios'
import CSVReader from 'react-csv-reader'
import { useDispatch, useSelector } from 'react-redux'
import BootstrapTable, {TableHeaderColumn} from 'react-bootstrap-table-next'
import { Button, Image, Alert, Form } from '@themesberg/react-bootstrap';
import paginationFactory from 'react-bootstrap-table2-paginator';
import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";
import filterFactory, { textFilter, selectFilter, dateFilter, Comparator} from 'react-bootstrap-table2-filter'; 
import template from "../assets/files/Bulk_upload_sheet.csv"
import { useHistory } from 'react-router-dom'
import { useToasts } from "react-toast-notifications";

import ReactLogo from '../assets/img/technologies/loader.png'
import Preloader from '../components/Preloader';

import { leadAssignerAddBulkLeadData } from '../actions/leadAssignerActions';
import { logout, managerList } from '../actions/userActions'
import {getBulkLeadList, editBulkLeadData, leadAssignerAssignBulkLeadData} from '../actions/bulkLeadActions'
import { LEADASSIGNER_ASSIGN_TO_MANAGER_RESET } from '../constants/leaadAssignerConstants'
import { LEADASSIGNER_BULK_LEAD_ADD_RESET } from '../constants/bulkLeadConstants'


const AssignBulkLeads = () => {
    const history = useHistory()
    var pageNum = 0
    const [leadData, setLeadData] = useState([])
    const [leadList, setLeadList] = useState([])
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState('')
    const [selectedManager, setSelectedManager] = useState('')
    const [selectedAssignTo, setSelectedAssignTo] = useState('')
    const [uploadError, setUploadError] = useState(false)
    const uploadRef = useRef()

    const { addToast } = useToasts();

    const dispatch = useDispatch()

    const userLogin = useSelector((state) => state.userLogin)
    const { userInfo } = userLogin

    const getBulkLead = useSelector((state) => state.getBulkLead)
    const {leadList: leads } = getBulkLead

    const fetchManagers = useSelector((state) => state.fetchManagers)
    const {managers } = fetchManagers 

    const leadAssignerAddBulkLead = useSelector((state) => state.leadAssignerAddBulkLead)
    const {leadAssignerLeads } = leadAssignerAddBulkLead 

    const assignToManagerBulkLead = useSelector((state) => state.assignToManagerBulkLead)
    const {success } = assignToManagerBulkLead 


    useEffect(()=>{
      if (userInfo && userInfo.role === "LeadAssigner") {
        dispatch(getBulkLeadList())
        dispatch(managerList())
      } else {
        history.push('/login')
      }
    }, [userInfo, history])



    useEffect(() => {
      const config = {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${userInfo.token}`,
        },
      }
      setTimeout(()=>{
       axios.get(`${process.env.REACT_APP_BASE_URL}/api/leadAssigner/bulklead/list`, config)
       .then(res => {
           const lead = res.data;
           setLeadData(lead && lead.leadsList)
           setLoading(false)                     
       }).catch(function (error) {
        if(error.response && error.response.status === 401){
          dispatch( logout())
        }
         setLoading(false)
         setError(error.response.data.message)
       })
     }, 0)
     

    }, [leadAssignerLeads])

    useEffect(()=>{
        setUploadError(false)
        try {
            if(leadList && leadList.length>0){
                if(leadList[0][0] === "Customer" || leadList[0][1] === "Enquiry Date" || leadList[0][2] === "Contact Number" || leadList[0][3] === "Alternate Number" ||  leadList[0][4] === "lead Source" || leadList[0][5] === "lead Type" || leadList[0][6] === "Status" || leadList[0][7] === "Remarks" || leadList[0][8] === "Next Follow Up Date"){

                  if((leadData.length+leadList.length)>502){
                    // dispatch(leadAssignerAddBulkLeadData(b.slice(0,(100 -leadData.length)), addToast))
                    alert("Exceeded the limit")
                  }else{
          
                dispatch(leadAssignerAddBulkLeadData(leadList, addToast))
                  }
                setLeadList([])
                history.push(`/leadassigner/bulkleads`)
                }else{
                  setUploadError(true)
                  history.push(`/leadassigner/bulkleads`)
                }
              }
            
        } catch (error) {
            history.push(`/leadassigner/bulkleads`)
            setUploadError(true)
        }
     
     
    },[leadList])

    useEffect(()=>{
      if(success){
        dispatch({
          type: LEADASSIGNER_BULK_LEAD_ADD_RESET
        })
        dispatch({
          type: LEADASSIGNER_ASSIGN_TO_MANAGER_RESET
        })
        setTimeout(()=>{
          setLeadData([])
        }, 2000)
        
      }
    },[success])

    const assignHandler = (e) => {
      dispatch(leadAssignerAssignBulkLeadData({selectedManager,selectedAssignTo, leads}, addToast))
    }

    const editTableHandler = (lead) => {
        dispatch(editBulkLeadData(lead))
    }

    console.log(selectedAssignTo)


    const options = {
          
      sizePerPage: 10,
      
    onPageChange: (page, sizePerPage) => {
      pageNum = page - 1
    }
  };


    const columns = [
        {
          dataField: "leadData",
          text: "Serial No.",
          formatter :(row,index, leadData) => {
            return (pageNum*10)+leadData+1
          },
          
        },
        {
          dataField: "customerName",
          text: "Customer",
          sort: true,
        //   filter: textFilter()
        },
        {
          dataField: "enquiryDate",
          text: "Enquiry Date",
          sort: true,
          formatter: (enquiryDate) => {
              if(enquiryDate){
            return enquiryDate && new Date(`${enquiryDate}`).toISOString().split('T')[0]
              }
          },
        //   filter: dateFilter({
        //     defaultValue: {comparator: Comparator.GT }
        //   })
        },
        {
          dataField: "contactNumber",
          text: "Contact Number",
        },
        {
          dataField: "alternateNumber",
          text: "Alternate Number",
        },
        {
          dataField: "leadScore",
          text: "lead Source",
        //   filter: textFilter()
        },
        {
          dataField: "leadType",
          text: "lead Type",
        //   filter: textFilter()
        },
        {
          dataField: "status",
          text: "Status",
        //   filter: textFilter()
        },
        {
          dataField: "remarks",
          text: "Remarks",
        //   filter: textFilter(),
        },
        {
          dataField: "nextFollowUp",
          text: "Next Follow Up Date",
          sort: true,
          formatter: (nextFollow) => {
              if(nextFollow){
            return nextFollow && new Date(`${nextFollow}`).toISOString().split('T')[0]
              }
          },
        //   filter: dateFilter({
        //     defaultValue: {comparator: Comparator.GT }
        //   })
        },
        {
          dataField: "managerRemarks",
          text: "Manager Remarks",
        //   filter: textFilter()
        },
        // {
        //   dataField: "",
        //   text: "",
        //   csvExport: false,
        //   formatter: (cell,row) => {
        //     return (
        //       <button className="btn btn-primary" onClick={ ()=>editTableHandler(row) }>Edit</button>
        //     );
        //   },
  
        // },
  
      ];

    return (
        <>
        {loading? (
      <div className={`preloader bg-softr flex-column justify-content-center align-items-center `}>
      <Image className="loader-element animate__animated animate__jackInTheBox" src={ReactLogo} height={70} />
      </div>
    ): error?  (<Alert variant="primary" className="text-center">
    {error}
  </Alert>):(
        <div>
          <div className="d-flex my-3 px-md-3 px-sm-1" >
          <a href={template} className='btn btn-primary' download style={{width: "150px"}}>Template</a>

            <div className="d-flex mx-5 ">

                <Form.Select onChange={(e)=>setSelectedManager(e.target.value)} style={{width: "200px"}} className='me-3'>
                  <option key={3000} value="">Select Manager</option>
                  {
                    managers && (managers.map((manager, i) => {
                        if(manager.projectId){
                      return (
                        <option key ={i} value={manager.managerId}>{manager.name}</option>
                      )
                        }
                    }))
                  }
                  
                </Form.Select>
                  <Form.Select onChange={(e)=>setSelectedAssignTo(e.target.value)} style={{width: "200px"}}>
                    <option key={3000} value="">Select Assign To</option>
                    <option key='Consultant' value='Consultant'>Consultant</option>
                    <option key='TeamLead' value='TeamLead'>TeamLead</option>
                  </Form.Select>
                <Button className='btn btn-primary ms-3' onClick={(e)=>assignHandler(e)} disabled={((leadData && leadData.length>0) && selectedManager && selectedAssignTo)? false : true} style={{width: "100px"}}> Assign</Button>
          </div>
          <div>
          <Form ref={uploadRef}>
            <CSVReader onFileLoaded={(data, fileInfo, originalFile) => {
                setLeadList(data)
                setTimeout(()=>{
                uploadRef.current.reset()
            },1000)
                }} />  
                </Form>
            <span style={{fontSize:"12px"}}>(Upload limit 500 leads)</span>
            </div>         
            {/* <img src={Profile1} alt="" /> */}
            </div>
            <div>
            { uploadError && <div className='mt-3'>
                    <Alert variant="danger">
                          {"Upload file format does not support"}
                        </Alert>
                    </div>}
    
                <BootstrapTable
                    bootstrap4
                    keyField="_id"
                    data={leadData}
                    columns={columns}
                    // cellEdit={ cellEditFactory({ mode: 'click', blurToSave: true })}
                    filter={ filterFactory() }
                    pagination={paginationFactory(options)}
                    striped
                    table-dark
                    />

             
        </div>
        </div>
        )
        }
        </>
    )
}

export default AssignBulkLeads
