import React,{useState, useEffect, useRef} from 'react'
import axios from 'axios'
import CSVReader from 'react-csv-reader'
import { useDispatch, useSelector } from 'react-redux'
import BootstrapTable from 'react-bootstrap-table-next'
import { Button, Image, Alert, Form } from '@themesberg/react-bootstrap';
import paginationFactory from 'react-bootstrap-table2-paginator';
import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";
import filterFactory from 'react-bootstrap-table2-filter'; 
import template from "../assets/files/Bulk_upload_sheet.csv"
import { useHistory } from 'react-router-dom'
import { useToasts } from "react-toast-notifications";

import ReactLogo from '../assets/img/technologies/loader.png'

import { addBulkLeadData, assignBulkLead, getBulkLeadList, editBulkLeadData, managerAssignBulkLeadToTeamlead } from '../actions/bulkLeadActions';
import { BULK_LEAD_ADD_RESET, BULK_LEAD_ASSIGN_RESET, BULK_LEAD_MANAGER_ASSIGN_TO_TEAMLEAD_REQUEST, BULK_LEAD_MANAGER_ASSIGN_TO_TEAMLEAD_RESET } from '../constants/bulkLeadConstants'
import { logout } from '../actions/userActions'


const BulkLeads = () => {
  var pageNum = 0

  const history = useHistory()

    const [leadData, setLeadData] = useState([])
    const [leadList, setLeadList] = useState([])
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState('')
    const [uploadError, setUploadError] = useState(false)
    const [assignLoading, setAssignLoading] = useState(false)
    const [selectedAssignTo, setSelectedAssignTo] = useState('')

    const dispatch = useDispatch()

    const uploadRef = useRef()

    const { addToast } = useToasts();

    const userLogin = useSelector((state) => state.userLogin)
    const { userInfo } = userLogin

    const getBulkLead = useSelector((state) => state.getBulkLead)
    const {leadList: leads } = getBulkLead

    const addBulkLead = useSelector((state) => state.addBulkLead)
    const {status } = addBulkLead

    const assignLeads = useSelector((state) => state.assignLeads)
    const {success : assignStatus, error: assignError } = assignLeads

    const assignBulkLeadToTeamlead = useSelector((state) => state.assignBulkLeadToTeamlead)
    const {success : assignToTeamleadStatus, error: assignToTeamleadError } = assignBulkLeadToTeamlead


    useEffect(()=>{
      if (userInfo && userInfo.role === "Manager") {
        dispatch(getBulkLeadList())
      } else {
        history.push('/login')
      }
    }, [userInfo, history])



    useEffect(() => {
      const config = {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${userInfo.token}`,
        },
      }
      setTimeout(()=>{
       axios.get(`${process.env.REACT_APP_BASE_URL}/api/bulkLead`, config)
       .then(res => {
           const lead = res.data;
           setLeadData(lead.bulkLeadList)
           setLoading(false)                     
       }).catch(function(error){
        if(error.response && error.response.status === 401){
          dispatch( logout())
        }
        setError(error.response && error.response.data.message)
      })
     }, 0)
     

    }, [status])

    useEffect(()=>{
     
      if(leadList && leadList.length>0){
        var b = []

        

         if( (leadList[0][0] === "Customer" || leadList[0][1] === "Enquiry Date" || leadList[0][2] === "Contact Number" || leadList[0][3] === "Alternate Number" || leadList[0][4] === "lead Source" || leadList[0][5] === "lead Type" || leadList[0][6] === "Status" || leadList[0][7] === "Remarks" || leadList[0][8] === "Next Follow Up Date")){
        // for(var i=1; i< leadList.length-1; i++){
        //   const a = {
        //       "serial": i,
        //       "customerName": leadList[i][0],
        //       "enquiryDate" : (leadList[i][1] && new Date(leadList[i][1]).toISOString())? new Date(leadList[i][1]).toISOString() : '',
        //       "contactNumber": leadList[i][2],
        //       "alternateNumber": leadList[i][3],
        //       "project": {
        //           "mainProject": leadList[i][4],
        //           "subProject": leadList[i][5]
        //       },
        //       "leadScore": leadList[i][6],
        //       "leadType": leadList[i][7],
        //       "status": leadList[i][8],
        //       "remarks": leadList[i][9],
        //       "nextFollowUp": (leadList[i][10] && new Date(leadList[i][10]).toISOString())? new Date(leadList[i][10]).toISOString():'',
        //       "managerRemarks": leadList[i][11],
        //       "payment":{
        //           "firstInstallment" : 0,
        //           "secondInstallment" : 0,
        //           "thirdInstallment" : 0
        //       },
        //       "cancellation":false,
        //       "totalSqft" : 0,
        //       "createdUser": userInfo._id,
        //       "assignedUser":""
        //   }
  
        //   b.push(a)
          
        //   }
          // if(leadData.length > 0){
          // setLeadData([...leadData, ...b])
          // }else{
          //   setLeadData(b)
          // }

          if((leadList.length+leadData.length)>502){
            // dispatch(addBulkLeadData(b.slice(0,(100 -leadData.length)), addToast))
            alert("Exceeded the limit")
          }else{
  
        dispatch(addBulkLeadData(leadList, addToast))
          }
        }else{
          setUploadError(true)
        }
      }
    },[leadList])

    useEffect(()=>{
      if( assignStatus || assignToTeamleadStatus){
        dispatch({
          type: BULK_LEAD_ADD_RESET
        })

        dispatch({
          type: BULK_LEAD_ASSIGN_RESET
        })

        dispatch({
          type: BULK_LEAD_MANAGER_ASSIGN_TO_TEAMLEAD_RESET
        })
        setAssignLoading(false)
        setTimeout(()=>{
          setLeadData([])
        }, 2000)
      }

    },[assignStatus, assignToTeamleadStatus])

    useEffect(()=>{
      if(assignError){
        dispatch({
          type: BULK_LEAD_ASSIGN_RESET
        })

        setAssignLoading(false)
      }
    },[assignError])


    const papaparseOptions = {
      header: true,
      // dynamicTyping: true,
      // skipEmptyLines: true,
      // transformHeader: header => header.toLowerCase().replace(/\W/g, "_")
    };


    const assignHandler = (e) => {
      if(selectedAssignTo === 'TeamLead'){
      dispatch(managerAssignBulkLeadToTeamlead(leadData, addToast))
      }else{
      dispatch(assignBulkLead(leadData, addToast))
      }
      setAssignLoading(true)
      uploadRef.current.reset()
    }

    const editTableHandler = (lead) => {
        dispatch(editBulkLeadData(lead))
    }

    const options = {
          
      sizePerPage: 10,
      
    onPageChange: (page, sizePerPage) => {
      pageNum = page - 1
    }
  };



    const columns = [
        {
          dataField: "_id",
          text: "Serial No.",
          formatter :(row,index, leadData) => {
            return (pageNum*10)+leadData+1
          },
          
        },
        {
          dataField: "customerName",
          text: "Customer",
          sort: true,
        //   filter: textFilter()
        },
        {
          dataField: "enquiryDate",
          text: "Enquiry Date",
          sort: true,
          formatter: (enquiryDate) => {
              if(enquiryDate){
            return enquiryDate && new Date(`${enquiryDate}`).toISOString().split('T')[0]
              }
          },
        //   filter: dateFilter({
        //     defaultValue: {comparator: Comparator.GT }
        //   })
        },
        {
          dataField: "contactNumber",
          text: "Contact Number",
        },
        {
          dataField: "alternateNumber",
          text: "Alternate Number"
        },
        {
          dataField: "assignedConsultant.userId.name",
          text: "Consultant"
        },
        {
          dataField: "leadScore",
          text: "lead Score",
        //   filter: textFilter()
        },
        {
          dataField: "leadType",
          text: "lead Type",
        //   filter: textFilter()
        },
        {
          dataField: "status",
          text: "Status",
        //   filter: textFilter()
        },
        {
          dataField: "remarks",
          text: "Remarks",
        //   filter: textFilter(),
        },
        {
          dataField: "nextFollowUp",
          text: "Next Follow Up Date",
          sort: true,
          formatter: (nextFollow) => {
              if(nextFollow){
            return nextFollow && new Date(`${nextFollow}`).toISOString().split('T')[0]
              }
          },
        //   filter: dateFilter({
        //     defaultValue: {comparator: Comparator.GT }
        //   })
        },
        {
          dataField: "managerRemarks",
          text: "Manager Remarks",
        //   filter: textFilter()
        },
        // {
        //   dataField: "",
        //   text: "",
        //   csvExport: false,
        //   formatter: (cell,row) => {
        //     return (
        //       <button className="btn btn-primary" onClick={ ()=>editTableHandler(row) }>Edit</button>
        //     );
        //   },
  
        // },
  
      ];


 

    return (
        <>
        {loading ||  assignLoading? (
      <div className={`preloader bg-softr flex-column justify-content-center align-items-center `}>
      <Image className="loader-element animate__animated animate__jackInTheBox" src={ReactLogo} height={70} />
      </div>
    ): error?  (<Alert variant="primary" className='text-center'>
    {error}
  </Alert>):(
        <div>
          <div className="d-flex justify-content-between align-item-center px-md-3 px-sm-1">
            
          <a href={template} className='btn btn-primary' download>Template</a>
          {/* <Button className='btn btn-primary ms-3' onClick={(e)=>assignHandler(e)} disabled={leadData && leadData.length>0? false : true}> Assign Leads</Button> */}
          <div className="d-flex mx-5 ">
                  <Form.Select onChange={(e)=>setSelectedAssignTo(e.target.value)} style={{width: "200px"}}>
                    <option key={3000} value="">Select Assign To</option>
                    <option key='Consultant' value='Consultant'>Consultant</option>
                    <option key='TeamLead' value='TeamLead'>TeamLead</option>
                  </Form.Select>
                <Button className='btn btn-primary ms-3' onClick={(e)=>assignHandler(e)} disabled={((leadData && leadData.length>0) && selectedAssignTo)? false : true} style={{width: "100px"}}> Assign</Button>
          </div>
         
          <div>
          <Form ref={uploadRef}>
            <CSVReader onFileLoaded={(data, fileInfo, originalFile) => {
                setLeadList(data)
                setTimeout(()=>{
                
            },1000)
                }} />  
                </Form>
            <span style={{fontSize:"12px"}}>(Upload limit 500 leads)</span>
            </div>
            {/* <img src={Profile1} alt="" /> */}
            </div>
            <div>
    
                <BootstrapTable
                    bootstrap4
                    keyField="_id"
                    data={leadData}
                    columns={columns}
                    // cellEdit={ cellEditFactory({ mode: 'click', blurToSave: true })}
                    filter={ filterFactory() }
                    pagination={paginationFactory(options)}
                    striped
                    table-dark
                    />

                   { uploadError && <div className='mt-3'>
                    <Alert variant="danger">
                          {"Upload file format does not support"}
                        </Alert>
                    </div>}
        </div>
        </div>
        )
        }
        </>
    )
}

export default BulkLeads
