import axios from 'axios'
import {
  CPM_LEAD_ADD_REQUEST,
  CPM_LEAD_ADD_FAIL,
  CPM_LEAD_ADD_SUCCESS,
  CPM_LEAD_UPDATE_REQUEST,
  CPM_LEAD_UPDATE_SUCCESS,
  CPM_LEAD_UPDATE_FAIL,
  CPM_LEAD_UPDATE_RESET,
  CPM_LEAD_DETAILS_REQUEST,
  CPM_LEAD_DETAILS_SUCCESS,
  CPM_LEAD_DETAILS_FAIL,
  CPM_LEAD_DETAILS_RESET,
  CPM_LEAD_LIST_REQUEST,
  CPM_LEAD_LIST_SUCCESS,
  CPM_LEAD_LIST_FAIL,
  CPM_LEAD_DELETE_REQUEST,
  CPM_LEAD_DELETE_SUCCESS,
  CPM_LEAD_DELETE_FAIL,

} from '../constants/cpManagerConstants'
import { logout } from './userActions'

/* Add Lead */
export const cpmAddLeadData = (leadData) => async (dispatch, getState) => {
    try {
      dispatch({
        type: CPM_LEAD_ADD_REQUEST,
      })

      const {
        userLogin: { userInfo },
      } = getState()
  
      const config = {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${userInfo.token}`,
        },
      }
  
      const { data } = await axios.post(
        `${process.env.REACT_APP_BASE_URL}/api/cpm/create/lead`,
        leadData,
        config
      )
  
      dispatch({
        type: CPM_LEAD_ADD_SUCCESS,
        payload: data,
      })
  
    } catch (error) {
       if(error.response && error.response.status === 401){
      dispatch( logout())
    }
      dispatch({
        type: CPM_LEAD_ADD_FAIL,
        payload:
          error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
      })
    }
  }

/* Update Lead */
export const cpmUpdateLeadData = (lead) => async (dispatch, getState) => {
    try {
      dispatch({
        type: CPM_LEAD_UPDATE_REQUEST,
      })
  
      const {
        userLogin: { userInfo },
      } = getState()
  
      const config = {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${userInfo.token}`,
        },
      }
  
      const { data } = await axios.put(
        `${process.env.REACT_APP_BASE_URL}/api/cpm/lead/edit/${lead._id}`,
        lead,
        config
      )
  
      dispatch({
        type: CPM_LEAD_UPDATE_SUCCESS,
        payload: data,
      })

      dispatch({
        type: CPM_LEAD_UPDATE_RESET
      })

      dispatch({
        type: CPM_LEAD_DETAILS_RESET
      })
      // dispatch({ type: CPM_LEAD_DETAILS_SUCCESS, payload: data })
    } catch (error) {
       if(error.response && error.response.status === 401){
      dispatch( logout())
    }
      const message =
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message
  
      dispatch({
        type: CPM_LEAD_UPDATE_FAIL,
        payload: message,
      })
    }
  }

