import React, {useState, useEffect} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useToasts } from "react-toast-notifications";

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleDown, faAngleUp, faArrowDown, faArrowUp, faEdit, faEllipsisH, faExternalLinkAlt, faEye, faTrashAlt } from '@fortawesome/free-solid-svg-icons';
import { Col, Row, Nav, Card, Image, Button, Table, Dropdown, ProgressBar, Pagination, ButtonGroup } from '@themesberg/react-bootstrap';
import { Link } from 'react-router-dom';

import { Routes } from "../routes";
import { pageVisits, pageTraffic, pageRanking } from "../data/tables";
import transactions from "../data/transactions";
import commands from "../data/commands";

import { consultantsUnderManager, consultantsUnderTeamLead, updateUser } from '../actions/userActions'
import { deleteUser } from '../actions/userActions';
import Preloader from '../components/Preloader';
import { USER_UPDATE_RESET } from '../constants/usersConstants';



const SetMilestone = ({history}) => {
    const { addToast } = useToasts();
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [bookingNumber, setBookingNumber] = useState(0)
    const [siteVisits, setSiteVisits] = useState(0)
    const [leads, setLeads] = useState(0)
    const [ userDetails, setUserDetails] = useState({})

    const dispatch = useDispatch();

    const userLogin = useSelector((state) => state.userLogin)
    const {userInfo: user } = userLogin

    const consultantsListUnderTeamLead = useSelector((state) => state.consultantsUnderTeamLead)
    const { loading, error, consultantList: users } = consultantsListUnderTeamLead

    const userUpdate = useSelector((state) => state.userUpdate)
    const { success } = userUpdate

    // const fetchConsultants = useSelector((state) => state.fetchConsultants)
    // const { loading, error, consultants: users } = fetchConsultants
    

    useEffect(() => {
        if(user && user.role === 'TeamLead'){
        dispatch(consultantsUnderTeamLead(user?.teamLeadId && user?.teamLeadId))
        }else{
            history.push('/login')
        }
    }, [dispatch, user])

    useEffect(() => {
        if(userDetails.name){
        dispatch(updateUser(userDetails._id,userDetails, addToast))
        }
    },[dispatch, userDetails])

    useEffect(() => {
        if(success){
            dispatch({
                type: USER_UPDATE_RESET
            })
            dispatch(consultantsUnderTeamLead(user.teamLeadId && user.teamLeadId))
        }
    }, [success])


    const bookingHandler = (e, user) => {
        e.preventDefault()
        user.userId.setMilestone ={
            bookings : bookingNumber? bookingNumber: user?.userId?.setMilestone?.bookings,
            leads : leads? leads : bookingNumber? bookingNumber * 6 : user?.userId?.setMilestone?.leads,
            siteVisits : siteVisits? siteVisits : bookingNumber? bookingNumber * 4 : user?.userId?.setMilestone?.siteVisits
        }
        setUserDetails(user.userId)
        setBookingNumber(0)
        setLeads(0)
        setSiteVisits(0)
        }
    
    return (
        <div>
            { loading ? <Preloader /> : (
                <Card border="light" className="table-wrapper table-responsive shadow-sm">
                <Card.Body className="pt-0">
                <Table hover className="user-table align-items-center">
                    <thead>
                    <tr>
                        <th className="border-bottom">Serial No.</th>
                        <th className="border-bottom">Employee Code</th>
                        <th className="border-bottom">Name</th>
                        <th className="border-bottom">Email Id</th>
                        <th className="border-bottom">Mobile no.</th> 
                        <th className="border-bottom">Bookings Number<br/>(per month)</th>  
                        <th className="border-bottom">Leads<br/>(per month)</th>  
                        <th className="border-bottom">Site Visits<br/>(per month)</th>            
                    </tr>
                    </thead>
                    <tbody>
                    {users && users?.consultantsList?.map((user, i) => 
                    <tr key={i}>
                    <td>
                        <span className="fw-normal">
                        {i+1}
                        </span>
                    </td>
                    <td>
                        <span className="fw-normal">
                        {user?.userId?.empCode}
                        </span>
                    </td>
                    <td>
                        <span className="fw-normal">
                        {user?.userId?.name}
                        </span>
                    </td>
                    <td>
                        <span className="fw-normal">
                        {user?.userId?.email}
                        </span>
                    </td>
                    <td>
                        <span className="fw-normal">
                        {user?.userId?.mobile}
                        </span>
                    </td>

                    <td>
                        <span className="fw-normal">
                        <input
                            type="number"
                            defaultValue={user?.userId?.setMilestone?.bookings}
                            onChange = { (e) => setBookingNumber(e.target.value)}
                        />
                        </span>
                    </td>

                    <td>
                        <span className="fw-normal">
                        <input
                            type="number"
                            defaultValue={user?.userId?.setMilestone?.siteVisits}
                            onChange={(e) => setSiteVisits(e.target.value)}
                        />
                        </span>
                    </td>

                    <td>
                        <span className="fw-normal">
                        <input
                            type="number"
                            defaultValue={user?.userId?.setMilestone?.leads}
                            onChange = {(e)=> setLeads(e.target.value)}
                        />
                        </span>
                    </td>

                    <td>
                        <div className="mt-3">
                            <Button variant="primary" type="submit" onClick = {(e) => bookingHandler(e,user)}>Save</Button>
                        </div>
                    </td>
                    {/* <td>
                        <span className="fw-normal">
                        {user.setMilestone.bookings * 6}
                        </span>
                    </td>

                    <td>
                        <span className="fw-normal">
                        {user.setMilestone.bookings * 4}
                        </span>
                    </td> */}
                    
                    {/* <td>
                        <Form.Control required type="text" placeholder="Enter your name" value={}/>
                    </td> */}
                    {/* <td>
                        <Dropdown as={ButtonGroup}>
                        <Dropdown.Toggle as={Button} split variant="link" className="text-dark m-0 p-0">
                            <span className="icon icon-sm">
                            <FontAwesomeIcon icon={faEllipsisH} className="icon-dark" />
                            </span>
                        </Dropdown.Toggle>
                        <Dropdown.Menu>
                            <Dropdown.Item>
                            <FontAwesomeIcon icon={faEye} className="me-2" /> View Details
                            </Dropdown.Item>
                            <Dropdown.Item>
                            <FontAwesomeIcon icon={faEdit} className="me-2" /> Edit
                            </Dropdown.Item>
                            <Dropdown.Item className="text-danger" onClick= {()=> deleteHandler(user._id)}>
                            <FontAwesomeIcon icon={faTrashAlt} className="me-2" /> Remove
                            </Dropdown.Item>
                        </Dropdown.Menu>
                        </Dropdown>
                    </td> */}
                    </tr>
                    )}
                    </tbody>
                </Table>
                {/* <Card.Footer className="px-3 border-0 d-lg-flex align-items-center justify-content-between">
                    <Nav>
                    <Pagination className="mb-2 mb-lg-0">
                        <Pagination.Prev>
                        Previous
                        </Pagination.Prev>
                        <Pagination.Item active>1</Pagination.Item>
                        <Pagination.Item>2</Pagination.Item>
                        <Pagination.Item>3</Pagination.Item>
                        <Pagination.Item>4</Pagination.Item>
                        <Pagination.Item>5</Pagination.Item>
                        <Pagination.Next>
                        Next
                        </Pagination.Next>
                    </Pagination>
                    </Nav>
                    {/* <small className="fw-bold">
                    Showing <b>{totalTransactions}</b> out of <b>25</b> entries
                    </small> 
                </Card.Footer> */}
                </Card.Body>
            </Card>
            )}
        </div>
    )
}

export default SetMilestone
