import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from 'react-redux'
import { Col, Row, Card, Form, Button } from '@themesberg/react-bootstrap';
import Preloader from "../components/Preloader";


import {fetchleadDetails, updateLeadData, deleteLead } from "./../actions/leadActions";
import { projects } from "./../actions/projectActions";
import { addCancelLeadData } from "../actions/cancellationLeadActions";
import { consultantList, teamLeadList } from "../actions/userActions";
import { LEAD_UPDATE_RESET } from "../constants/leadsConstants";



export default ({match, history}) => {
    const leadId = match.params.id

    var gapi = window.gapi

    var CLIENT_ID = "1013896874235-1htic5jfq2uhg6s8kjt7130t98a0kd86.apps.googleusercontent.com"
    var API_KEY = "AIzaSyBoJQl0S2P5iI5LxS4FQThPKm8b5rVagwc"
    var DISCOVERY_DOCS = ["https://www.googleapis.com/discovery/v1/apis/calendar/v3/rest"]
    var SCOPES = "https://www.googleapis.com/auth/calendar.events"

    const [createdUser, setCreatedUser] = useState('')
    const [assignedUser, setAssignedUser] = useState('')
    const [consultant, setConsultant] = useState('')
    const [enquiryDate, setEnquiryDate] = useState('')
    const [customer, setCustomer] = useState('')
    const [contactNumber, setContactNumber] = useState('')
    const [alternateNumber, setAlternateNumber] = useState('')
    const [mainProject, setMainProject] = useState('')
    const [subProject, setSubProject] = useState('')
    const [projectId, setProjectId] = useState('')
    const [leadScore, setLeadScore] = useState('')
    const [leadType, setLeadType] = useState('')
    const [status, setStatus] = useState('')
    const [remarks, setRemarks] = useState('')
    const [nextFollow, setNextFollow] = useState('')
    const [managerRemarks, setManagerRemarks] = useState('')
    var [firstInstallment, setFirstInstallment] = useState(0)
    var [secondInstallment, setSecondInstallment] = useState(0)
    var [thirdInstallment, setThirdInstallment] = useState(0)
    const [totalSqft, setTotalSqft] = useState(0)
    const [index, setIndex] = useState(0)
    const [remainder, setRemainder] = useState(false)
    const [totalPrice, setTotalPrice] = useState(0)
    const [assignTo, setAssignTo] = useState('Consultant')
    const [selectedTeamlead, setSelectedTeamlead] = useState('')

    const dispatch = useDispatch()

    const leadDetails = useSelector((state) => state.leadDetails)
    const { loading, error, leadInfo } = leadDetails

    const updateLead = useSelector((state) => state.updateLead)
    const { loading: loadingUpdate, error: errorUpdate , success: successUpdate } = updateLead

    const Projects = useSelector((state) => state.fetchProjects)
    const { loading: loadingProject, error: errorProject , projectInfo } = Projects

    const fetchConsultants = useSelector((state) => state.fetchConsultants)
    const { loading: loadingConsultants, error: errorConsultants, consultants: consultantsList } = fetchConsultants

    const fetchTeamLeads = useSelector((state) => state.fetchTeamLeads)
    const { loading: loadingTeamLead, error: errorTeamLead, teamlead: teamleadsList } = fetchTeamLeads

    const userLogin = useSelector((state) => state.userLogin)
    const { userInfo: user } = userLogin

    useEffect(()=>{
      if (user && user.role === 'LeadAssigner') {
        dispatch(consultantList())
        dispatch(teamLeadList())
        dispatch(projects())
      } else {
        history.push('/login')
      }
    }, [user, history])

    useEffect(() => {
      if(successUpdate){
        dispatch({
          type: LEAD_UPDATE_RESET
        })
        history.push('/leads')
      }
    },[successUpdate])

    useEffect(()=>{
    }, [mainProject, subProject])

    // useEffect(() => {
    //   dispatch(projects())
    //   dispatch(consultantList())
    //   // dispatch(managers())
    //   // dispatch(teamLeads())
    // },[])
  
    useEffect(() => {
     
      if (!leadInfo || leadInfo._id !== leadId) {
        dispatch(fetchleadDetails(leadId))
      }else{ 
        setCreatedUser(leadInfo.createdUser)
        setAssignedUser(leadInfo?.assignedConsultant)
        setSelectedTeamlead(leadInfo?.assignedTeamLead)
        setAssignTo(leadInfo?.assignedConsultant? 'Consultant' : 'TeamLead')
        setConsultant(leadInfo.consultantName)
        setCustomer(leadInfo.customerName)
        setEnquiryDate(leadInfo.enquiryDate)
        setContactNumber(leadInfo.contactNumber)
        setAlternateNumber(leadInfo.alternateNumber)
        setMainProject(leadInfo.project && leadInfo.project.mainProject)
        setSubProject(leadInfo.project && leadInfo.project.subProject)
        setProjectId(leadInfo.project && leadInfo.project.projectId)
        setLeadType(leadInfo.leadType)
        setLeadScore(leadInfo.leadScore)
        setStatus(leadInfo.status)
        setRemarks(leadInfo.remarks)
        setNextFollow(leadInfo.nextFollowUp)
        setManagerRemarks(leadInfo.managerRemarks)
        setFirstInstallment(leadInfo.payment.firstInstallment)
        setSecondInstallment(leadInfo.payment.secondInstallment)
        setThirdInstallment(leadInfo.payment.thirdInstallment)
        setTotalSqft(leadInfo.totalSqft? leadInfo.totalSqft : 0)
        setRemainder(leadInfo.remainder? leadInfo.remainder : false)
        setTotalPrice(leadInfo.totalPrice? leadInfo.totalPrice: 0)
      }
    }, [dispatch, leadInfo, successUpdate])

    function findSubProject(project) {
      return project.mainProject === mainProject
    }

    useEffect(()=>{
      if(mainProject){
        if(projectInfo)
        {
          const main = projectInfo.findIndex(findSubProject);
        setIndex(main)

      }
      }
    },[mainProject])


    // const subProjectHandler = () => {
    //   if(projectInfo)
    //   {
    //     const main = projectInfo.findIndex(findSubProject);
    //   setIndex(main)
    // }
      
    // }

    // useEffect(()=>{
    //   subProjectHandler()
    // }, [mainProject])
    const consultantHandler = (e) => {

      setSelectedTeamlead('')
      const x = consultantsList.filter(c => c.consultantId === e.target.value)[0]
      if(x){
        let a = projectInfo.filter(pro => pro._id === x.projectId)[0] 
        if(a){   
       setMainProject(a.mainProject)
       setProjectId(a.projectId)
       setSubProject(a.subProject[0])
       setAssignedUser(x.consultantId)
         setConsultant(x.name)
        }
        }else{
          setConsultant('')

        }
     }

     const deleteHandler = (e) => {
       e.preventDefault()
      if (window.confirm('Are you sure?')){
      dispatch(deleteLead(leadId))
      }
     }


    const submitHandler = (e) => {
      e.preventDefault()
        dispatch(
          updateLeadData({
            _id: leadId,
            createdUser,
            assignedConsultant: assignTo === 'Consultant'?assignedUser : null,
            assignedTeamLead: assignTo === 'TeamLead'? selectedTeamlead : null,
            customerName:customer,
            consultantName:consultant,
            enquiryDate:new Date(new Date(enquiryDate).getTime()+330*60000),
            contactNumber,
            alternateNumber,
            project: {
              mainProject: mainProject,
              subProject: subProject,
              projectId: projectId
            },
            leadScore,
            leadType,
            status,
            remarks,
            managerRemarks,
            nextFollowUp: new Date(new Date(nextFollow).getTime()+330*60000),
            payment:{
              firstInstallment: firstInstallment,
              secondInstallment: secondInstallment,
              thirdInstallment: thirdInstallment
            },
            totalSqft,
            totalPrice, 
            cancellation : false,
            remainder
          }))
    }

    const projectHandler = (e) => {
      setMainProject(e.target.value)
    }

    const cancelLeadHandler = (e, id) => {
      e.preventDefault()
      if (window.confirm('Are you sure?')) {
          dispatch(addCancelLeadData({
            _id: leadId,
            createdUser,
            assignedConsultant: assignedUser,
            customerName:customer,
            consultantName: consultant,
            enquiryDate : enquiryDate? new Date(new Date(enquiryDate).getTime()+330*60000) : null,
            contactNumber,
            alternateNumber,
            project: {
              mainProject: mainProject,
              subProject: subProject,
              projectId: projectId
            },
            leadScore,
            leadType,
            status,
            remarks,
            managerRemarks,
            nextFollowUp: nextFollow? new Date(new Date(nextFollow).getTime()+330*60000) : null,
            payment:{
              firstInstallment: firstInstallment,
              secondInstallment: secondInstallment,
              thirdInstallment: thirdInstallment
            },
            totalSqft, 
            totalPrice,
            cancellation : true
          }))
          dispatch(deleteLead(leadId)) 
          history.push('/leads')
        }

  }


  const handleClick = () => {
    gapi.load('client:auth2', () => {
      // console.log('loaded client')

      gapi.client.init({
        apiKey: API_KEY,
        clientId: CLIENT_ID,
        discoveryDocs: DISCOVERY_DOCS,
        scope: SCOPES,
      })

      gapi.client.load('calendar', 'v3', () => console.log('bam!'))

      gapi.auth2.getAuthInstance().signIn()
      .then(() => {
        
        var event = {
          'summary': leadInfo.customerName,
          'location': '',
          'description': leadInfo.project.subProject,
          'start': {
            'dateTime': new Date(nextFollow).toISOString().replace('Z',''),
            'timeZone': 'Asia/Kolkata'
          },
          'end': {
            'dateTime': new Date(nextFollow).toISOString().replace('Z',''),
            'timeZone': 'Asia/Kolkata'
          },
          'recurrence': [
            'RRULE:FREQ=DAILY;COUNT=1'
          ],
          'attendees': [
            {'email': 'deve.metromindz@gmail.com'}
          ],
          'reminders': {
            'useDefault': false,
            'overrides': [
              {'method': 'email', 'minutes': 24 * 60},
              {'method': 'popup', 'minutes': 10}
            ]
          }
        }

        var request = gapi.client.calendar.events.insert({
          'calendarId': 'primary',
          'resource': event,
        })

        request.execute(event => {
          // console.log(event)
          window.open(event.htmlLink)
        })
        

        /*
            Uncomment the following block to get events
        */
        /*
        // get events
        gapi.client.calendar.events.list({
          'calendarId': 'primary',
          'timeMin': (new Date()).toISOString(),
          'showDeleted': false,
          'singleEvents': true,
          'maxResults': 10,
          'orderBy': 'startTime'
        }).then(response => {
          const events = response.result.items
          console.log('EVENTS: ', events)
        })
        */
    

      })
    })
  }

  const assignToHandler = (e) => {
    setAssignTo(e.target.value)
    if(e.target.value === 'Consultant') {
      setConsultant('')
    }else{
      setSelectedTeamlead('')
    }
   }  


  const teamleadHandler = (e) => {
    
    setConsultant('')
    let y = teamleadsList.filter(t=>t.teamLeadId === e.target.value)[0]
    if(y){
      
    let a = projectInfo.filter(pro => pro._id === y.projectId)[0]

    if(a){
    setMainProject(a.mainProject)
    setProjectId(a.projectId)
    setSubProject(a.subProject[0])
    setSelectedTeamlead(y.teamLeadId)
    }
     }else{
      setSelectedTeamlead('')

    }
   }


  return (
    <>
    { loading && loadingProject ? (<Preloader />) : error? (<p>{error}</p>) :(
    <Card border="light" className="bg-white shadow-sm mb-4">
      <Card.Body>
        <h5 className="mb-4">Lead information</h5>
        <Form onSubmit={(e)=>submitHandler(e)}>
          <Row>
            <Col md={6} className="mb-3">
              <Form.Group id="customer">
                <Form.Label>Customer*</Form.Label>
                <Form.Control  required type="text" placeholder="Enter customer name" value={customer || ''} onChange={(e) => {
                  if(e.target.value.match("^[a-zA-Z ]*$") != null){
                  setCustomer(e.target.value)}}}/>
              </Form.Group>
            </Col>
            <Col md={6} className="mb-3">
              <Form.Group id="lastName">
                <Form.Label>Enquiry Date*</Form.Label>
                <Form.Control required type="datetime-local" placeholder="Enter Enquiry Date" defaultValue={enquiryDate && enquiryDate.split('.')[0]}  onChange={(e) => {setEnquiryDate(e.target.value)}}/>
              </Form.Group>
            </Col>
          </Row>
          
          <Row>
          
            <Col md={6} className="mb-3">
              <Form.Group id="contact">
                <Form.Label>Contact no.*</Form.Label>
                <Form.Control required type="tel" minLength="10" maxLength="10" placeholder="987654321" value={contactNumber || ''} onChange={(e) =>  {if(e.target.value.match("[0-9]+") != null){
                    setContactNumber(e.target.value)
                                   }}}/>
              </Form.Group>
            </Col>

            <Col md={6} className="mb-3">
              <Form.Group id="number">
                <Form.Label>Alternate no.</Form.Label>
                <Form.Control minLength="10" maxLength="10" type="tel" placeholder="987654321" value={alternateNumber} onChange={(e) => {if(e.target.value.match("[0-9]+") != null){
                  setAlternateNumber(e.target.value)
                }}}/>
              </Form.Group>
            </Col>

            {/* <Col md={6} className="mb-3">
              <Form.Group id="consultant">
              <Form.Label>Assign to Consultant</Form.Label>
                <Form.Select value={consultantsList && consultantsList.findIndex(c=>c.name===consultant)} onChange={(e) => consultantHandler(e)}>
                  <option key={300} value="">Select</option>
                 
                  {
                    consultantsList && (consultantsList.map((consultant, i) => {
                      if(consultant.projectId){
                       return <option key ={i} value={i}>{consultant.name}</option>
                      }
                    }))
                  }
                  
                </Form.Select>
              </Form.Group>
            </Col> */}

            <Col md={6} className="mb-3">
              <Form.Group id="consultant">
              <Form.Label>Assign to</Form.Label>
                <Form.Select value={assignTo } onChange={(e) => assignToHandler(e)}>
                  <option key={"Consultant"} value={"Consultant"}>Consultant</option>
                  <option key={"TeamLead"} value={"TeamLead"}>TeamLead</option>
                </Form.Select>
              </Form.Group>
            </Col>
          {assignTo === 'Consultant'?
            
            <Col md={6} className="mb-3">
              <Form.Group id="consultant">
              <Form.Label>Consultant*</Form.Label>
                <Form.Select required value={assignedUser?._id} onChange={(e) => consultantHandler(e)}>
                  <option key={300} value="">Select</option>
                  {
                    consultantsList && (consultantsList.map((consultant, i) => {
                      if(consultant.projectId){
                        return <option key ={i} value={consultant.consultantId}>{consultant.name}</option>
                    }
                    }))
                  }
                  
                </Form.Select>
              </Form.Group>
            </Col>
                :
            <Col md={6} className="mb-3">
              <Form.Group id="teamlead">
              <Form.Label>TeamLead*</Form.Label>
                <Form.Select required value={selectedTeamlead} onChange={(e) => teamleadHandler(e)}>
                  <option key={300} value="">Select</option>
                  {
                    teamleadsList && (teamleadsList.map((teamlead, i) => {
                      if(teamlead.projectId){
                        return <option key ={i} value={teamlead.teamLeadId}>{teamlead.name}</option>
                    }
                    }))
                  }
                  
                </Form.Select>
              </Form.Group>
            </Col>
           
            }

            
            </Row>

            <Row>
            <Col md={6} className="mb-3">
              <Form.Group id="project">
                <Form.Label>Project</Form.Label>
                <Form.Control type="text" placeholder="Main Project" value={mainProject} disabled/>
                {/* <Form.Select value={mainProject? mainProject : ''} onChange={(e) => projectHandler(e)}>
                <option key ={99999} value={''}>Select</option>
                  {
                    projectInfo && (projectInfo.map((pro, i) => {
                      return (
                        <option key ={i} value={pro.mainProject}>{pro.mainProject}</option>
                      )
                    }))
                  }
                  
                </Form.Select> */}
              </Form.Group>
            </Col>

            <Col md={6} className="mb-3">
              <Form.Group id="subProject">
                <Form.Label>Sub Project*</Form.Label>
                <Form.Select required value={subProject} onChange={(e) => setSubProject(e.target.value)} disabled={!mainProject}>
                  <option key ={99999} value={''}>Select</option>
                  {
                    projectInfo && projectInfo[index] && (projectInfo[index].subProject.map((pro, i) => {
                      return (
                        <option key ={i} value={pro}>{pro}</option>
                      )
                    }))
                  }
                </Form.Select>
              </Form.Group>
            </Col>
          </Row>

          <Row>
            <Col md={6} className="mb-3">
              <Form.Group id="email">
                <Form.Label>Lead Source</Form.Label>
                {/* <Form.Control required type="text" placeholder="Lead Source" value={leadScore || ''} onChange={(e) => setLeadScore(e.target.value)}/> */}
                <Form.Select value={leadScore || ''} onChange={(e) => setLeadScore(e.target.value)}>
                  <option value="SMS">SMS</option>
                  <option value="Paper Insert">Paper Insert</option>
                  <option value="Housing">Housing</option>
                  <option value="Suleka">Suleka</option>
                  <option value="BRC marketing">BRC marketing</option>
                  <option value="LGA">LGA</option>
                  <option value="Self generation">Self generation</option>
                  <option value="MM">MM</option>
                  <option value="Reference">Reference</option>
                  <option value="Walk-in">Walk-in</option>
                  <option value="Utopia">Utopia</option>
                  <option value="Activity">Activity</option>
                  <option value="99 acres">99 acres</option>
                  <option value="MagicBricks">MagicBricks</option>
                  <option value="Society Landline">Society Landline</option>
                  <option value="Society Mobile">Society Mobile</option>
                  <option value="BRC Landline">BRC Landline</option>
                  <option value="BRC Mobile">BRC Mobile</option>
                  <option value="Google Ads">Google Ads</option>
                </Form.Select>
              </Form.Group>
            </Col>

            <Col md={6} className="mb-3">
              <Form.Group id="leadType">
                <Form.Label>LEAD TYPE</Form.Label>
                <Form.Select value={leadType || ''} onChange={(e) => setLeadType(e.target.value)}>
                  <option value="Hot">Hot</option>
                  <option value="Cold">Cold</option>
                  <option value="Warm">Warm</option>
                </Form.Select>
              </Form.Group>
            </Col>
            
          </Row>

          <Row>
            <Col md={6} className="mb-3">
              <Form.Group id="status">
                <Form.Label>STATUS</Form.Label>
                <Form.Select value={status || ''} onChange={(e) => setStatus(e.target.value)}>
                    <option value="Yet to Visit">Yet to Visit</option>
                    <option value="Visit Confirmed">Visit Confirmed</option>
                    <option value="Detail Shared">Detail Shared</option>
                    <option value="Not Interested">Not Interested</option>
                    <option value="RFR">RFR</option>
                    <option value="Booked">Booked</option>
                    <option value="Site Visit">Site Visit</option>
                  <option value="Office Visit">Office Visit</option>
                  <option value="Direct Visit">Direct Visit</option>
                </Form.Select>
              </Form.Group>
            </Col>

            <Col md={6} className="mb-3">
              <Form.Group id="lastName">
                <Form.Label>Next Follow Up Date</Form.Label>
                <Form.Control type="datetime-local" placeholder="Next Follow Up Date" min={enquiryDate} defaultValue={nextFollow && new Date(nextFollow).toISOString().split('.')[0]} onChange={(e) => {setNextFollow(e.target.value)}}/>
              </Form.Group>
            </Col>                     
         </Row>

         <Row>  
          <Col md={6} className="mb-3">
              <Form.Group id="email">
                <Form.Label>Remarks</Form.Label>
                <Form.Control  type="text" placeholder="Remarks" value={remarks} onChange={(e) => setRemarks(e.target.value)}/>
              </Form.Group>
            </Col> 

            <Col md={6} className="mb-3">
              <Form.Group id="email">
                <Form.Label>Manager Remarks</Form.Label>
                <Form.Control type="text" placeholder="Manager Remarks" value={managerRemarks || ''} onChange={(e) => setManagerRemarks(e.target.value)}/>
              </Form.Group>
            </Col> 
          </Row>

         <Row>
          <h6 className="mb-4">Payment</h6>
            <Col md={4} className="mb-3">
            <Form.Group id="firstInstallment">
                <Form.Label>1st Installment</Form.Label>
                  <Form.Control type="number" placeholder="1st Installment" value={firstInstallment} onChange={(e) => setFirstInstallment(firstInstallment = parseInt(e.target.value || 0))}/>
                </Form.Group>
            </Col>
            <Col md={4} className="mb-3">
            <Form.Group id="secondInstallment">
                <Form.Label>2nd Installment</Form.Label>
                  <Form.Control type="number" placeholder="2nd Installment" value={secondInstallment} onChange={(e) => setSecondInstallment(secondInstallment = parseInt(e.target.value || 0))}/>
                </Form.Group>
            </Col>                                 
            <Col md={4} className="mb-3">
            <Form.Group id="thirdInstallment">
                <Form.Label>3rd Installment</Form.Label>
                  <Form.Control type="number" placeholder="3rd Installment" value={thirdInstallment} onChange={(e) => setThirdInstallment(thirdInstallment = parseInt(e.target.value ) || 0)}/>
                </Form.Group>
            </Col>
         </Row>

         <Row>
          <h6 className="mb-4">Payment</h6>
          <Col md={4} className="mb-3">
            <Form.Group id="totalprice">
                <Form.Label>Total Price</Form.Label>
                  <Form.Control type="number" placeholder="Total Price" value={totalPrice} onChange={(e) => setTotalPrice(e.target.value)}/>
                </Form.Group>
            </Col>                                 
            <Col md={4} className="mb-3">
            <Form.Group id="totalsqft">
                <Form.Label>Total Sqft</Form.Label>
                  <Form.Control type="number" placeholder="Total Sqft" value={totalSqft} onChange={(e) => setTotalSqft(e.target.value)}/>
                </Form.Group>
            </Col>
         </Row>

          <div className="mt-3 d-flex justify-content-between">
            <div>
            <Button variant="primary" type="submit">Save</Button>
            <Button variant="primary" className="ms-3" type="submit" onClick={handleClick} disabled={!nextFollow}>Reminder</Button>
            {/* <Button className="ms-3"  variant="primary" type="button" onClick = {(e)=>cancelLeadHandler(e, leadId)}>Cancellation</Button> */}
            </div>
            <div>
            <Button variant ="danger" onClick={(e)=>{deleteHandler(e)}}>Delete</Button>
            </div>
          </div>
        </Form>
      </Card.Body>
    </Card>
  
    )}
    </>
  );
};
